.nav-wrapper, .page-footer {
  background-color:rgb(53, 89, 89) !important;
}
.parallax-container {
  min-height: 550px;
  line-height: 0;
  height: auto !important;
  color: rgba(255,255,255,.9);
  clear: both;
}
.sidenav-trigger {
  color: white;
}
.grey-text {
  font-size: x-large;
  font-weight: 500;
  line-height: 2.25rem;
}
@media screen and (min-width: 996px) {
  .button-collapse {
    display: none;
  }
}
.panel-About {
    left: 30%
}
.panel-Services {
    left: 40%
}
.panel-Events {
    left: 50%
}
.panel-Contact {
    left: 60%
}

@media (max-width: 1028px) {
    div[class*='panel'] {
        width: 25%
    }
    .panel-About {
        left: 0
    }
    .panel-Services {
        left: 25%
    }
    .panel-Events {
        left: 50%
    }
    .panel-Contact {
        left: 75%
    }
}
div[class*='AutoRotatingCarousel-carouselWrapper'] {
    box-shadow: 0 0 60px 60px #888888 !important
}
.player-wrapper {
    width: 100%; 
    height: 425px; 
}

video {
    object-fit:fill;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}



